body{
  font-family: sans-serif;
  padding: 10px;
}
.header h1{
  margin-bottom: 3px;
  margin-top: 0px;
}
header hr{
  margin-top: 0px;
  margin-bottom: 0px;
}
.biography{
  width: 78%;
  display: inline-block;
  font-size: 14px;
  line-height: 1.6;
}
.summary{
  display: inline-block;
  vertical-align: top;
  width: 20%;
  margin-top: 15px;
}
.summary-table{
  border: 1px solid #a2a9b1;
  background-color: #f8f9fa;
  padding: 1px;
  font-size: 14px;
}
.summary-table thead th{
  background-color: #b0c4de;
  padding-top: 3px;
  padding-bottom: 3px;
  font-size: 16px;
}
.summary-table tbody tr:first-child{
    text-align: center;
}
.summary-table tbody th{
  text-align: left;
}
.photo{
  width: 85%
}


